.editor-container {
  position: relative;
  width: 400px;
  height: 400px;
  margin: 50px auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

.image-container {
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: relative;
}

.clipped-content {
  width: 100%;
  height: 100%;
}

.control-point {
  position: absolute;
  width: 16px;
  height: 16px;
  background-color: red;
  border-radius: 50%;
  cursor: pointer;
}

.control-point:hover {
  background-color: orange;
}
